// src/components/ProductList.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  TextField,
  TableFooter,
  TablePagination,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Box
} from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';

// Вспомогательный хук для "дебаунсинга" значения
function useDebounce(value, delay) {
  const [debouncedValue, setDebouncedValue] = useState(value);

  useEffect(() => {
    const handler = setTimeout(() => setDebouncedValue(value), delay);
    return () => clearTimeout(handler);
  }, [value, delay]);

  return debouncedValue;
}

const ProductList = () => {
  const [products, setProducts] = useState([]);
  const [filter, setFilter] = useState('');
  const [categoryFilter, setCategoryFilter] = useState('');
  const [categoriesList, setCategoriesList] = useState([]);
  // Новый state для курса валют (1 EUR = ? UAH)
  const [exchangeRate, setExchangeRate] = useState(30);
  const navigate = useNavigate();

  // Параметры пагинации:
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  // Загрузка продуктов
  const fetchProducts = async () => {
    try {
      const response = await axios.get('http://13.51.233.141/admin/api/products');
      setProducts(response.data);
    } catch (error) {
      console.error('Error fetching products: ', error);
    }
  };

  // Загрузка категорий для фильтрации
  const fetchCategories = async () => {
    try {
      const response = await axios.get('http://13.51.233.141/admin/api/categories');
      setCategoriesList(response.data);
    } catch (error) {
      console.error('Error fetching categories: ', error);
    }
  };

  // Загрузка текущего курса валют из БД
  const fetchExchangeRate = async () => {
    try {
      const response = await axios.get('http://13.51.233.141/admin/api/exchange/exchange-rate');
      setExchangeRate(response.data.exchangeRate);
    } catch (error) {
      console.error("Error fetching exchange rate:", error);
    }
  };

  useEffect(() => {
    fetchProducts();
    fetchCategories();
    fetchExchangeRate();
  }, []);

  const deleteProduct = async (id) => {
    try {
      await axios.delete(`http://13.51.233.141/admin/api/products/${id}`);
      fetchProducts();
    } catch (error) {
      console.error('Error deleting product: ', error);
    }
  };

  // Обработчик обновления курса валют на сервере
  const updateExchangeRate = async () => {
    try {
      const response = await axios.put(
        'http://13.51.233.141/admin/api/exchange/exchange-rate',
        { newExchangeRate: exchangeRate }
      );
      console.log("Exchange rate updated:", response.data);
      // Обновляем курс в state на значение, полученное от сервера
      setExchangeRate(response.data.exchangeRate);
      // Обновляем список продуктов, если требуется
      fetchProducts();
    } catch (error) {
      console.error("Error updating exchange rate:", error);
    }
  };

  // Фильтр с "дебаунсом" (500 мс)
  const debouncedFilter = useDebounce(filter, 500);
  const debouncedCategoryFilter = useDebounce(categoryFilter, 500);

  // Фильтруем локально (по полю title, SKU и категории)
  const filteredProducts = products.filter((product) =>
    (
      product.title.toLowerCase().includes(debouncedFilter.toLowerCase()) ||
      (product.sku && product.sku.toLowerCase().includes(debouncedFilter.toLowerCase()))
    ) &&
    product.category.toLowerCase().includes(debouncedCategoryFilter.toLowerCase())
  );

  // Данные, которые рендерятся на текущей странице:
  const displayedProducts = filteredProducts.slice(
    page * rowsPerPage,
    page * rowsPerPage + rowsPerPage
  );

  // Обработчики пагинации
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <div>
      <Button
        variant="contained"
        color="primary"
        component={Link}
        to="/products/new"
        sx={{ backgroundColor: "#077c8f" }}
      >
        Додати товар
      </Button>

      {/* Фильтр по названию товара / SKU */}
      <TextField
        label="Пошук товарів (назва або SKU)"
        variant="outlined"
        fullWidth
        value={filter}
        onChange={(e) => setFilter(e.target.value)}
        sx={{ marginTop: 2, marginBottom: 2 }}
      />

      {/* Фильтр по категории в виде Select */}
      <FormControl fullWidth sx={{ marginBottom: 2 }}>
        <InputLabel id="category-filter-label">Фільтр за категорією</InputLabel>
        <Select
          labelId="category-filter-label"
          label="Фільтр за категорією"
          value={categoryFilter}
          onChange={(e) => setCategoryFilter(e.target.value)}
        >
          <MenuItem value="">Всі категорії</MenuItem>
          {categoriesList.map((cat) => (
            <MenuItem key={cat._id} value={cat.name}>
              {cat.name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      {/* Поле ввода курса валют и кнопка для обновления */}
      <Box sx={{ display: 'flex', gap: 2, alignItems: 'center', marginBottom: 2 }}>
        <TextField
          label="Курс валют (1 EUR = ? UAH)"
          variant="outlined"
          fullWidth
          type="number"
          value={exchangeRate}
          onChange={(e) => setExchangeRate(Number(e.target.value))}
        />
        <Button variant="contained" onClick={updateExchangeRate} sx={{ backgroundColor: "#077c8f" }}>
          Оновити курс
        </Button>
      </Box>

      <TableContainer component={Paper} sx={{ marginTop: 2 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell>Назва товару</TableCell>
              <TableCell>Категорія</TableCell>
              <TableCell>Тип</TableCell>
              <TableCell>Ціна (EUR)</TableCell>
              <TableCell>Ціна (UAH)</TableCell>
              <TableCell>Кількість</TableCell>
              <TableCell>Дії</TableCell>
            </TableRow>
          </TableHead>

          <TableBody>
            {displayedProducts.map((product) => (
              <TableRow key={product.id}>
                <TableCell>{product.id}</TableCell>
                <TableCell>{product.title}</TableCell>
                <TableCell>{product.category}</TableCell>
                <TableCell>
                  {product.type === "sale"
                    ? "Розпродаж"
                    : product.type === "leaders"
                    ? "Лідери продаж"
                    : ''}
                </TableCell>
                <TableCell>{product.costEUR}</TableCell>
                <TableCell>{(product.costUAH).toFixed(2)}</TableCell>
                <TableCell>{product.quantity}</TableCell>
                <TableCell>
                  <Button
                    variant="outlined"
                    component={Link}
                    to={`/products/edit/${product.id}`}
                    sx={{ mr: 1 }}
                  >
                    Редагувати
                  </Button>
                  <Button
                    variant="outlined"
                    color="error"
                    onClick={() => deleteProduct(product.id)}
                  >
                    Видалити
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>

          <TableFooter>
            <TableRow>
              <TablePagination
                rowsPerPageOptions={[5, 10, 25, 50]}
                colSpan={8}
                count={filteredProducts.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onPageChange={handleChangePage}
                onRowsPerPageChange={handleChangeRowsPerPage}
              />
            </TableRow>
          </TableFooter>
        </Table>
      </TableContainer>
    </div>
  );
};

export default ProductList;
