import React, { useState } from 'react';
import axios from 'axios';
import { Button, Typography, CircularProgress } from '@mui/material';

const Integrations = () => {
  const [status, setStatus] = useState('');
  const [isLoading, setIsLoading] = useState(false);

  const exchangeStocks = async () => {
    try {
      setStatus('Начинается процесс обновления остатков...');
      setIsLoading(true);

      const response = await axios.get('http://13.51.233.141/admin/api/integration/update-stock');
      
      setStatus('Остатки успешно обновлены.');
      console.log('Stocks updated: ', response.data);
    } catch (error) {
      setStatus(`Ошибка при обновлении остатков: ${error.message}`);
      console.error('Error fetching products: ', error);
    } finally {
      setIsLoading(false);
    }
  };

  const createProductsCRM = async () => {
    try {
      setStatus('Начинается процесс создания товаров из CRM...');
      setIsLoading(true);

      const response = await axios.get('http://13.51.233.141/admin/api/create/create-prod');
      
      setStatus('Товары успешно обновлены.');
      console.log('Products updated: ', response.data);
    } catch (error) {
      setStatus(`Ошибка при обновлении товаров: ${error.message}`);
      console.error('Error updating products: ', error);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div>
      <Button
        variant="contained"
        color="primary"
        onClick={exchangeStocks}
        disabled={isLoading}
        sx={{backgroundColor:"#077c8f"}}

      >
        Оновити залишки
      </Button>
      <Button
        variant="contained"
        color="primary"
        onClick={createProductsCRM}
        disabled={isLoading}
        sx={{ backgroundColor: "#077c8f" }}
      >
        Створити товари
      </Button>
      <div style={{ marginTop: '20px' }} >
        {isLoading && <CircularProgress />}
        {status && <Typography variant="body1">{status}</Typography>}
      </div>
    </div>
  );
};

export default Integrations;
