// src/components/Users/UserPromoCodes.js
import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import {
  Typography,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Button,
  Stack,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import axios from 'axios';

const UserPromoCodes = () => {
  const { id } = useParams(); // id пользователя
  const [promoCodes, setPromoCodes] = useState([]);

  // Загружаем промокоды пользователя
  const fetchPromoCodes = async () => {
    try {
      const response = await axios.get(`http://13.51.233.141/admin/api/promocode/${id}/get-promocodes`);
      // Предполагается, что контроллер возвращает объект { promoCodes: [...] }
      setPromoCodes(response.data.promoCodes);
    } catch (error) {
      console.error("Ошибка при получении промокодов:", error);
    }
  };

  useEffect(() => {
    fetchPromoCodes();
  }, [id]);

  // Удаляем промокод
  const handleDeletePromoCode = async (promoCodeId) => {
    try {
      await axios.delete(`http://13.51.233.141/admin/api/promocode/${id}/promocodes/${promoCodeId}`);
      // Обновляем список после удаления
      fetchPromoCodes();
    } catch (error) {
      console.error("Ошибка при удалении промокода:", error);
    }
  };

  return (
    <div>
      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 2 }}>
        <Typography variant="h4">
        Промокоди користувача {id}
        </Typography>
        <Stack direction="row" spacing={2}>
          <Button variant="contained" component={Link} to={`/users/${id}/promocodes/new`}>
          Додати промокод
          </Button>
          <Button variant="contained" component={Link} to="/users">
          Повернутись до користувачів
          </Button>
        </Stack>
      </Stack>

      {promoCodes.map((promo) => (
        <Accordion key={promo._id}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography>
              {promo.title} — Знижка: {promo.discount}% — Дійсний до: {new Date(promo.validUntil).toLocaleDateString()} — Кількість: {promo.quantity}
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Stack direction="row" spacing={2}>
              <Button
                variant="contained"
                component={Link}
                to={`/users/${id}/promocodes/${promo._id}/edit`}
              >
                Редагувати
              </Button>
              <Button
                variant="contained"
                color="error"
                onClick={() => handleDeletePromoCode(promo._id)}
              >
                Видалити
              </Button>
            </Stack>
          </AccordionDetails>
        </Accordion>
      ))}
    </div>
  );
};

export default UserPromoCodes;
