// src/components/Orders/OrderForm.js

import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import axios from 'axios';
import {
  Paper,
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  IconButton,
  Autocomplete,
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';

const OrderForm = () => {
  // Берём из URL: userId => id, orderId (опционально)
  const { id, orderId } = useParams();
  const navigate = useNavigate();

  // Состояние заказа (для создания/редактирования)
  const [orderData, setOrderData] = useState({
    area: '',
    city: '',
    warehouse: '',
    number_phone: '',
    status: '',
    order_number: '',
    products: [],
    totalCost: 0,
  });

  // Список товаров для выбора
  const [productsList, setProductsList] = useState([]);

  // Для выбора товара и количества
  const [selectedProductId, setSelectedProductId] = useState('');
  const [selectedQuantity, setSelectedQuantity] = useState(1);

  // 1. Загружаем список товаров
  useEffect(() => {
    axios
      .get('http://13.51.233.141/admin/api/products')
      .then((response) => setProductsList(response.data))
      .catch((error) => console.error('Error fetching products:', error));
  }, []);

  // 2. Если orderId есть => редактирование => грузим данные заказа
  useEffect(() => {
    if (orderId) {
      axios
        .get(`http://13.51.233.141/admin/api/users/${id}/orders/${orderId}`)
        .then((response) => {
          setOrderData(response.data); // подставляем полученные данные в форму
        })
        .catch((error) => console.error('Error fetching order:', error));
    }
  }, [id, orderId]);

  // 3. Автопересчёт стоимости
  useEffect(() => {
    const total = orderData.products.reduce((sum, p) => sum + p.cost * p.quantity, 0);
    setOrderData((prevData) => ({ ...prevData, totalCost: total }));
  }, [orderData.products]);

  // Обработка изменений в текстовых полях
  const handleOrderChange = (e) => {
    const { name, value } = e.target;
    setOrderData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  // Добавление товара в список
  const handleAddProduct = () => {
    if (!selectedProductId) return;
    const product = productsList.find((p) => p._id === selectedProductId);
    if (!product) return;

    const productEntry = {
      productId: product._id,
      title: product.title,
      color: product.color || '',
      size: product.size || '',
      quantity: Number(selectedQuantity),
      cost: product.cost,
      img: product.imageURL && product.imageURL.length > 0 ? product.imageURL[0] : '',
      sku: product.sku || '',
      id: product.id,
    };

    setOrderData((prevData) => ({
      ...prevData,
      products: [...prevData.products, productEntry],
    }));
    setSelectedProductId('');
    setSelectedQuantity(1);
  };

  // Удаление товара из списка
  const handleRemoveProduct = (index) => {
    const newProducts = orderData.products.filter((_, i) => i !== index);
    setOrderData((prevData) => ({
      ...prevData,
      products: newProducts,
    }));
  };

  // 4. Сохранение (POST при создании, PUT при редактировании)
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (orderId) {
        // Редактирование
        await axios.put(`http://13.51.233.141/admin/api/users/${id}/orders/${orderId}`, orderData);
      } else {
        // Создание
        await axios.post(`http://13.51.233.141/admin/api/users/${id}/orders`, orderData);
      }
      // После сохранения возвращаемся к списку заказов
      navigate(`/users/orders/${id}`);
    } catch (error) {
      console.error('Error saving order:', error);
    }
  };

  return (
    <Paper sx={{ padding: 3 }}>
      <Typography variant="h6" gutterBottom>
        {orderId ? `Редагувати замовлення #${orderId} для Користувача ${id}` : `Створити Замовлення для Користувача ${id}`}
      </Typography>
      <Box
        component="form"
        onSubmit={handleSubmit}
        sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}
      >
        {/* Информация о доставке */}
        <Typography variant="subtitle1">Інформація про Доставку</Typography>
        <TextField
          label="Область"
          name="area"
          value={orderData.area}
          onChange={handleOrderChange}
        />
        <TextField
          label="Місто"
          name="city"
          value={orderData.city}
          onChange={handleOrderChange}
        />
        <TextField
          label="Відділення"
          name="warehouse"
          value={orderData.warehouse}
          onChange={handleOrderChange}
        />
        <TextField
          label="Номер Телефону"
          name="number_phone"
          value={orderData.number_phone}
          onChange={handleOrderChange}
        />

        {/* Основные данные заказа */}
        <Typography variant="subtitle1">Деталі Замовлення</Typography>
        <TextField
          label="Номер Замовлення"
          name="order_number"
          value={orderData.order_number}
          onChange={handleOrderChange}
        />
        <TextField
          label="Статус"
          name="status"
          value={orderData.status}
          onChange={handleOrderChange}
        />

        {/* Выбор товаров */}
        <Typography variant="subtitle1">Товари</Typography>
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={5}>
            <Autocomplete
              options={productsList}
              getOptionLabel={(option) => option.title}
              value={productsList.find((product) => product._id === selectedProductId) || null}
              onChange={(event, newValue) => {
                setSelectedProductId(newValue ? newValue._id : '');
              }}
              renderInput={(params) => (
                <TextField {...params} label="Обрати Товар" variant="outlined" fullWidth />
              )}
            />
          </Grid>
          <Grid item xs={3}>
            <TextField
              type="number"
              label="Кількість"
              value={selectedQuantity}
              onChange={(e) => setSelectedQuantity(e.target.value)}
            />
          </Grid>
          <Grid item xs={4}>
            <Button variant="contained" onClick={handleAddProduct}>
              Додати Товар
            </Button>
          </Grid>
        </Grid>

        {/* Список добавленных товаров */}
        {orderData.products.length > 0 && (
          <Box>
            <Typography variant="subtitle1">Товари у Замовленні</Typography>
            {orderData.products.map((prod, index) => (
              <Box
                key={index}
                sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 1 }}
              >
                <Typography>
                  {prod.title} — Кількість: {prod.quantity} — Ціна: {prod.cost}
                </Typography>
                <IconButton onClick={() => handleRemoveProduct(index)} size="small">
                  <DeleteIcon />
                </IconButton>
              </Box>
            ))}
          </Box>
        )}

        <Typography variant="subtitle1">Загальна Вартість: {orderData.totalCost}</Typography>

        <Button type="submit" variant="contained" color="primary">
          {orderId ? 'Зберегти Зміни' : 'Створити Замовлення'}
        </Button>
      </Box>
    </Paper>
  );
};

export default OrderForm;