// src/components/CategoryForm.js
import React, { useState, useEffect } from 'react';
import { TextField, Button, Paper, Box, Typography, IconButton } from '@mui/material';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import DeleteIcon from '@mui/icons-material/Delete';

const CategoryForm = () => {
  const { id } = useParams();
  const isEditMode = Boolean(id);
  const navigate = useNavigate();

  const [formData, setFormData] = useState({
    name: '',
    linkName: '',
    subcategories: [],
  });

  useEffect(() => {
    if (isEditMode) {
      axios
        .get(`http://13.51.233.141/admin/api/categories/${id}`)
        .then((response) => setFormData(response.data))
        .catch((error) => console.error('Error fetching category:', error));
    }
  }, [id, isEditMode]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSubcategoryChange = (index, field, value) => {
    const newSubcategories = [...formData.subcategories];
    newSubcategories[index] = {
      ...newSubcategories[index],
      [field]: value,
    };
    setFormData((prevData) => ({
      ...prevData,
      subcategories: newSubcategories,
    }));
  };

  const handleBrandChange = (subcategoryIndex, brandIndex, value) => {
    const newSubcategories = [...formData.subcategories];
    const currentBrands = newSubcategories[subcategoryIndex].brands || [];
    currentBrands[brandIndex] = value;
    newSubcategories[subcategoryIndex] = {
      ...newSubcategories[subcategoryIndex],
      brands: [...currentBrands],
    };
    setFormData({
      ...formData,
      subcategories: newSubcategories,
    });
  };

  const addBrand = (subcategoryIndex) => {
    const newSubcategories = [...formData.subcategories];
    const currentBrands = newSubcategories[subcategoryIndex].brands || [];
    newSubcategories[subcategoryIndex] = {
      ...newSubcategories[subcategoryIndex],
      brands: [...currentBrands, ''],
    };
    setFormData({
      ...formData,
      subcategories: newSubcategories,
    });
  };

  const removeBrand = (subcategoryIndex, brandIndex) => {
    const newSubcategories = [...formData.subcategories];
    const currentBrands = newSubcategories[subcategoryIndex].brands || [];
    const updatedBrands = currentBrands.filter((_, i) => i !== brandIndex);
    newSubcategories[subcategoryIndex] = {
      ...newSubcategories[subcategoryIndex],
      brands: updatedBrands,
    };
    setFormData({
      ...formData,
      subcategories: newSubcategories,
    });
  };

  const addSubcategory = () => {
    setFormData((prevData) => ({
      ...prevData,
      subcategories: [...prevData.subcategories, { name: '', linkName: '', brands: [] }],
    }));
  };

  const removeSubcategory = (index) => {
    const newSubcategories = formData.subcategories.filter((_, i) => i !== index);
    setFormData((prevData) => ({
      ...prevData,
      subcategories: newSubcategories,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (isEditMode) {
        await axios.put(`http://13.51.233.141/admin/api/categories/${id}`, formData);
      } else {
        await axios.post('http://13.51.233.141/admin/api/categories', formData);
      }
      navigate('/categories');
    } catch (error) {
      console.error('Error saving category:', error);
    }
  };

  return (
    <Paper sx={{ padding: 3 }}>
      <Typography variant="h6" gutterBottom>
        {isEditMode ? 'Редагування Категорії' : 'Створення Категорії'}
      </Typography>
      <Box component="form" onSubmit={handleSubmit} sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        <TextField
          label="Назва"
          name="name"
          value={formData.name}
          onChange={handleChange}
          required
        />
        <TextField
          label="Назва (англ.)"
          name="linkName"
          value={formData.linkName}
          onChange={handleChange}
          required
        />

        <Typography variant="subtitle1">Підкатегорії</Typography>
        {formData.subcategories.map((subcategory, index) => (
          <Box key={index} sx={{ border: '1px solid #ccc', padding: 2, borderRadius: 1, mb: 2 }}>
            <Box sx={{ display: 'flex', gap: 2, alignItems: 'center' }}>
              <TextField
                label="Назва Підкатегорії"
                value={subcategory.name}
                onChange={(e) => handleSubcategoryChange(index, 'name', e.target.value)}
                required
              />
              <TextField
                label="Назва Підкатегорії (англ.)"
                value={subcategory.linkName}
                onChange={(e) => handleSubcategoryChange(index, 'linkName', e.target.value)}
                required
              />
              <IconButton onClick={() => removeSubcategory(index)}>
                <DeleteIcon />
              </IconButton>
            </Box>

            {/* Блок для брендів у підкатегорії */}
            <Typography variant="subtitle2" sx={{ mt: 2 }}>
              Бренди
            </Typography>
            {subcategory.brands && subcategory.brands.map((brand, brandIndex) => (
              <Box key={brandIndex} sx={{ display: 'flex', gap: 2, alignItems: 'center', mt: 1 }}>
                <TextField
                  label={`Бренд ${brandIndex + 1}`}
                  value={brand}
                  onChange={(e) => handleBrandChange(index, brandIndex, e.target.value)}
                  required
                />
                <IconButton onClick={() => removeBrand(index, brandIndex)}>
                  <DeleteIcon />
                </IconButton>
              </Box>
            ))}
            <Button variant="outlined" onClick={() => addBrand(index)} sx={{ mt: 1 }}>
              Додати бренд
            </Button>
          </Box>
        ))}
        <Button variant="outlined" onClick={addSubcategory}>
          Додати підкатегорію
        </Button>

        <Button type="submit" variant="contained" color="primary" sx={{ backgroundColor: "#077c8f" }}>
          {isEditMode ? 'Редагувати Категорію' : 'Додати Категорію'}
        </Button>
      </Box>
    </Paper>
  );
};

export default CategoryForm;
