// src/components/Users/PromoCodeForm.js
import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import {
    TextField, Button, Stack, Typography, Paper
} from '@mui/material';
import axios from 'axios';

const PromoCodeForm = () => {
    const { id, promoCodeId } = useParams(); // id - пользователь, promoCodeId (опционально) - промокод
    const navigate = useNavigate();
    const isEdit = !!promoCodeId;

    const [formData, setFormData] = useState({
        title: '',
        discount: '',
        validUntil: '',
        quantity: '',
    });

    // Если редактируем, загружаем данные промокода
    useEffect(() => {
        if (isEdit) {
            axios.get(`http://13.51.233.141/admin/api/promocode/${id}/get-promocodes`)
                .then(response => {
                    const promo = response.data.promoCodes.find(p => p._id === promoCodeId);
                    if (promo) {
                        setFormData({
                            title: promo.title,
                            discount: promo.discount,
                            // Приводим дату к формату YYYY-MM-DD для input type="date"
                            validUntil: new Date(promo.validUntil).toISOString().split('T')[0],
                            quantity: promo.quantity,
                        });
                    }
                })
                .catch(error => console.error("Ошибка при получении промокода:", error));
        }
    }, [id, promoCodeId, isEdit]);

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (isEdit) {
                await axios.put(`http://13.51.233.141/admin/api/promocode/${id}/promocodes/${promoCodeId}`, formData);
            } else {
                await axios.post(`http://13.51.233.141/admin/api/promocode/${id}/promocodes`, formData);
            }
            navigate(`/users/${id}/promocodes`);
        } catch (error) {
            console.error("Ошибка при сохранении промокода:", error);
        }
    };

    return (
        <Paper sx={{ p: 3 }}>
            <Typography variant="h5" sx={{ mb: 2 }}>
                {isEdit ? "Редагувати промокод" : "Додати промокод"}
            </Typography>
            <form onSubmit={handleSubmit}>
                <Stack spacing={2}>
                    <TextField
                        label="Назва"
                        name="title"
                        value={formData.title}
                        onChange={handleChange}
                        required
                    />
                    <TextField
                        label="Знижка (%)"
                        name="discount"
                        type="number"
                        value={formData.discount}
                        onChange={handleChange}
                        required
                    />
                    <TextField
                        label="Дійсний до"
                        name="validUntil"
                        type="date"
                        InputLabelProps={{ shrink: true }}
                        value={formData.validUntil}
                        onChange={handleChange}
                        required
                    />
                    <TextField
                        label="Кількість"
                        name="quantity"
                        type="number"
                        value={formData.quantity}
                        onChange={handleChange}
                        required
                    />
                    <Stack direction="row" spacing={2}>
                        <Button variant="contained" type="submit">
                            {isEdit ? "Зберегти" : "Додати"}
                        </Button>
                        <Button variant="outlined" onClick={() => navigate(`/users/${id}/promocodes`)}>
                            Скасування
                        </Button>
                    </Stack>
                </Stack>
            </form>
        </Paper>
    );
};

export default PromoCodeForm;
