// src/components/Users/UserOrders.js

import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import {
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Stack,
} from '@mui/material';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import axios from 'axios';

const UserOrders = () => {
  const { id } = useParams(); // id пользователя
  const [orders, setOrders] = useState([]);

  // Загружаем заказы пользователя
  const fetchOrders = async () => {
    try {
      const response = await axios.get(`http://13.51.233.141/admin/api/users/${id}/orders`);
      setOrders(response.data);
    } catch (error) {
      console.error("Error fetching orders:", error);
    }
  };

  useEffect(() => {
    fetchOrders();
  }, [id]);

  // Удаляем заказ
  const handleDeleteOrder = async (orderId) => {
    try {
      await axios.delete(`http://13.51.233.141/admin/api/users/${id}/orders/${orderId}`);
      // Обновляем список после удаления
      fetchOrders();
    } catch (error) {
      console.error("Error deleting order:", error);
    }
  };

  return (
    <div>
      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ mb: 2 }}>
        <Typography variant="h4">
          Історія Замовлень для Користувача {id}
        </Typography>
        <Stack direction="row" spacing={2}>
          {/* <Button variant="contained" component={Link} to={`/users/${id}/orders/new`}>
            Додати замовлення
          </Button> */}
          <Button variant="contained" component={Link} to="/users">
            Повернутись до Користувачів
          </Button>
        </Stack>
      </Stack>

      {orders.map((order) => (
        <Accordion key={order._id}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />}>
            <Typography>
              Замовлення #{order.order_number} — Загальна ціна: {order.totalCost} — Статус: {order.status}
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography variant="subtitle1">Інформація про доставку</Typography>
            <Typography>Область: {order.area}</Typography>
            <Typography>Місто: {order.city}</Typography>
            <Typography>Відділення: {order.warehouse}</Typography>

            <Typography variant="subtitle1" sx={{ mt: 2 }}>Товари</Typography>
            <TableContainer component={Paper}>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell>ID</TableCell>
                    <TableCell>Назва</TableCell>

                    <TableCell>Кількість</TableCell>
                    <TableCell>Ціна</TableCell>
                    <TableCell>Артикул</TableCell>
                    <TableCell>Зображення</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {order.products.map((product, index) => (
                    <TableRow key={index}>
                      <TableCell>{product.id}</TableCell>
                      <TableCell>{product.title}</TableCell>

                      <TableCell>{product.quantity}</TableCell>
                      <TableCell>{product.cost}</TableCell>
                      <TableCell>{product.sku}</TableCell>
                      <TableCell>
                        {product.img ? (
                          <img
                            src={product.img}
                            alt={product.title}
                            style={{ width: '50px', height: '50px' }}
                          />
                        ) : (
                          'N/A'
                        )}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>

            {/* Кнопки Edit / Delete */}
            <Stack direction="row" spacing={2} sx={{ mt: 2 }}>
              <Button
                variant="contained"
                component={Link}
                to={`/users/${id}/orders/${order._id}/edit`}
              >
                Редагувати
              </Button>
              <Button
                variant="contained"
                color="error"
                onClick={() => handleDeleteOrder(order._id)}
              >
                Видалити
              </Button>
            </Stack>
          </AccordionDetails>
        </Accordion>
      ))}
    </div>
  );
};

export default UserOrders;
