import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  Typography
} from '@mui/material';
import { Link, useNavigate } from 'react-router-dom';

const formatDate = (dateString) => {
  if (!dateString) return '';
  const date = new Date(dateString);
  const day = date.getDate().toString().padStart(2, '0');
  const month = (date.getMonth() + 1).toString().padStart(2, '0');
  const year = date.getFullYear();
  const hours = date.getHours().toString().padStart(2, '0');
  const minutes = date.getMinutes().toString().padStart(2, '0');
  return `${day}-${month}-${year} ${hours}:${minutes}`;
};

const UserList = () => {
  const [users, setUsers] = useState([]);
  const navigate = useNavigate();

  const fetchUsers = async () => {
    try {
      const response = await axios.get('http://13.51.233.141/admin/api/users');
      setUsers(response.data);
    } catch (error) {
      console.error('Error fetching users: ', error);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const deleteUser = async (id) => {
    try {
      await axios.delete(`http://13.51.233.141/admin/api/users/${id}`);
      fetchUsers();
    } catch (error) {
      console.error('Error deleting user: ', error);
    }
  };

  return (
    <div>
      <Typography variant="h4" gutterBottom>
        Користувачі
      </Typography>
      <Button 
        variant="contained" 
        color="primary" 
        component={Link} 
        to="/users/new" 
        sx={{ backgroundColor: "#077c8f" }}
      >
        Додати Користувача
      </Button>
      <TableContainer component={Paper} sx={{ marginTop: 2 }}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Дата реєстрації</TableCell>
              <TableCell>Дата оновлення даних</TableCell>
              <TableCell>Номер Телефону</TableCell>
              <TableCell>Ім'я</TableCell>
              <TableCell>Прізвище</TableCell>
              <TableCell>Email</TableCell>
              <TableCell>Дії</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {users.map((user) => (
              <TableRow key={user._id}>
                <TableCell>{formatDate(user.createdAt)}</TableCell>
                <TableCell>{formatDate(user.updatedAt)}</TableCell>
                <TableCell>{user.number_phone.name}</TableCell>
                <TableCell>{user.firstname.name}</TableCell>
                <TableCell>{user.lastname.name}</TableCell>
                <TableCell>{user.email.name}</TableCell>
                <TableCell>
                  <Button
                    variant="outlined"
                    component={Link}
                    to={`/users/edit/${user._id}`}
                    sx={{ mr: 1 }}
                  >
                    Редагувати
                  </Button>
                  <Button
                    variant="outlined"
                    color="error"
                    onClick={() => deleteUser(user._id)}
                    sx={{ mr: 1 }}
                  >
                    Видалити
                  </Button>
                  <Button
                    variant="outlined"
                    color="secondary"
                    onClick={() => navigate(`/users/orders/${user._id}`)}
                    sx={{ mr: 1 }}
                  >
                    Замовлення
                  </Button>
                  <Button
                    variant="outlined"
                    color="info"
                    component={Link}
                    to={`/users/${user._id}/promocodes`}
                  >
                    Промокоди
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default UserList;
