// src/components/Messages/FeedbackMessages.js
import React, { useState, useEffect } from 'react';
import {
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    Typography,
} from '@mui/material';
import axios from 'axios';

const FeedbackMessages = () => {
    const [messages, setMessages] = useState([]);

    const fetchMessages = async () => {
        try {
            const response = await axios.get('http://13.51.233.141/admin/api/message/get-messages');
            // Предполагается, что контроллер возвращает объект { data: messages }
            setMessages(response.data.data);
        } catch (error) {
            console.error('Ошибка при получении сообщений:', error);
        }
    };

    useEffect(() => {
        fetchMessages();
    }, []);

    return (
        <Paper sx={{ p: 3, mt: 3 }}>
            <Typography variant="h5" gutterBottom>
                Повідомлення зворотного зв'язку
            </Typography>
            <TableContainer>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Відправник</TableCell>
                            <TableCell>Номер телефону</TableCell>
                            <TableCell>Повідомлення</TableCell>
                            <TableCell>Дата</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {messages.map((msg) => (
                            <TableRow key={msg._id}>
                                <TableCell>{msg.client_name}</TableCell>
                                <TableCell>{msg.phone}</TableCell>
                                <TableCell>{msg.comment}</TableCell>
                                <TableCell>{new Date(msg.created_at).toLocaleString()}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Paper>
    );
};

export default FeedbackMessages;