// src/components/Users/UserForm.js
import React, { useState, useEffect } from 'react';
import { TextField, Button, Paper, Box, Typography } from '@mui/material';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';

const UserForm = () => {
  const { id } = useParams();
  const isEditMode = Boolean(id);
  const navigate = useNavigate();

  // Инициализируем все поля как объекты
  const [formData, setFormData] = useState({
    number_phone: { name: '', title: 'Номер телефону' },
    firstname: { name: '', title: "Ім'я" },
    lastname: { name: '', title: "Прізвище" },
    email: { name: '', title: "Email" },
    password: { name: '', title: "Пароль" },
  });

  useEffect(() => {
    if (isEditMode) {
      axios.get(`http://13.51.233.141/admin/api/users/${id}`)
        .then(response => {
          // Предполагается, что данные пользователя приходят в нужном формате
          setFormData(response.data);
        })
        .catch(error => console.error('Error fetching user:', error));
    }
  }, [id, isEditMode]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevData => {
      const field = prevData[name];
      if (field && typeof field === 'object' && 'name' in field) {
        return {
          ...prevData,
          [name]: { ...field, name: value }
        };
      } else {
        return {
          ...prevData,
          [name]: value
        };
      }
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      if (isEditMode) {
        await axios.put(`http://13.51.233.141/admin/api/users/${id}`, formData);
      } else {
        await axios.post('http://13.51.233.141/admin/api/users', formData);
      }
      navigate('/users');
    } catch (error) {
      console.error('Error saving user:', error);
    }
  };

  return (
    <Paper sx={{ padding: 3 }}>
      <Typography variant="h6" gutterBottom>
        {isEditMode ? 'Редагувати Користувача' : 'Додати Користувача'}
      </Typography>
      <Box component="form" onSubmit={handleSubmit} sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
        <TextField
          label="Номер Телефону"
          name="number_phone"
          value={formData.number_phone.name}
          onChange={handleChange}
          required
        />
        <TextField
          label="Ім'я"
          name="firstname"
          value={formData.firstname.name}
          onChange={handleChange}
          required
        />
        <TextField
          label="Прізвище"
          name="lastname"
          value={formData.lastname.name}
          onChange={handleChange}
        />
        <TextField
          label="Email"
          name="email"
          value={formData.email.name}
          onChange={handleChange}
        />
        <TextField
          label="Пароль"
          name="password"
          type="text"
          value={formData.password.name}
          onChange={handleChange}
          // required={!isEditMode}
        />
        <Button type="submit" variant="contained" color="primary" sx={{ backgroundColor: "#077c8f" }}
        >
          {isEditMode ? 'Редагувати Користувача' : 'Додати Користувача'}
        </Button>
      </Box>
    </Paper>
  );
};

export default UserForm;
